import { Component, Input, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { QuestionBase } from '../common/question-base';

@Component({
  selector: 'app-dynamic-form',
  templateUrl: './dynamic-form.component.html',
  styleUrls: ['./dynamic-form.component.scss'],
})
export class DynamicFormComponent implements OnInit {

  @Input() question!: any;
  @Input() form!: FormGroup;
  @Input() listColumns!: any[];
  // get isValid() { return this.form.controls[this.question.key].valid; }

  constructor(){}

  ngOnInit(){}

  addListFields(question, index){
    if(question.listColumns === ''){
      question.listColumns = [{
        type: 'text',
        field: index,
        value: ''
      }];
    } else {
      question.listColumns.push({
        type: 'text',
        field: index,
        value: ''
      });
    }
  }

  removeListFields(question, index){
    question.listColumns.splice(1, index);
  }
}
